.App {
    text-align: center;
  }
  
  .header {
    background-color: #333;
    padding: 20px;
    display: flex;
    justify-content: center;
  }
  
  .menu-item {
    color:white;
    margin-right: 15px;
    position: relative;
    cursor: pointer;
  }
  
  .submenu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #555;
    padding: 10px;
    list-style-type: none;
  }
  
  .menu-item:hover .submenu {
    display: block;
  }
  
  .menu-item:hover {
    background-color: #555;
  }
  