.registration-form {
    width: 300px;
    margin: 20px auto;
    padding: 20px;
    /*     border: 1px solid #ccc;
    border-radius: 5px;
*/
  }
  
  label {
    display: flex;
    margin-bottom: 10px;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    box-sizing: border-box;
  }
  
  button {
    background-color: #4caf50;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }
  