.menu-item {
    color: white;
    margin-right: 15px;
    position: relative;
    cursor: pointer;
  }
  
  .submenu {
    display: none;
    position: absolute;
    top: calc(100% - 17px); /* 100%; */
    left: 0;
    background-color: #555;
    padding: 15px;
    list-style-type: none;
  }
  
  .menu-item:hover .submenu {
    display: block;
  }
  